	        justify-content: center;
	margin-top: 100px;
	-webkit-align-items: center;
	        align-items: center;
	
}
.mt-4{
	margin-top: 4.5rem!important;
}


.cajacatalogo .card{
	position: relative;	
	background: transparent;	
	transition:0.3s ease-in-out;
	border: none;
	
} 
 

.cajacatalogo .card .imgBx{
	position: relative;
	top: -60px;
	z-index: 1;
	margin: auto;
}

.cajacatalogo .card .imgBx img{
	max-width: 150px;
	width: 100%;
	border-radius: 4px;
}

.cajacatalogo .card .content{
	position: relative;
	margin-top: -140px;
	padding: 10px 15px;
	color: #111;
	visibility: hidden;
	opacity: 0;
	transition: 0.3s ease-in-out;
	text-align: center;
}
.cajacatalogo .card:hover .content{
	visibility: visible;
	opacity: 1;
	margin-top: -40px;
	transition-delay: 0.3s;
}

.cajacatalogo .card .content p{
	text-align:center;
}
.cajacatalogo .card .content h3{
	text-align:center;
}
